body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  text-align: center;
  background-color: #1dbfed;
  color: white;
}

.jumbotron {
  background-color: #1dbfed;
  padding: 10px;
  margin-top: -50px;
}

.bgbox {
  background-color: #7de3fc;
  height: 200px;
  line-height: 160px;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 10px;
}

.bgbox h3 {
  vertical-align: middle;
  display: inline-block;
}

.clickable:hover {
  cursor: pointer;
}

.questionbox {
  padding: 10px 10px;
  margin-bottom: 20px;
}

input[type="text"] {
  margin: 5px;
  padding: 5px;
  color: black;
  text-align: center;
  border-radius: 5px;
  border-style: solid;
  border-color: white;
}

form {
  margin: 20px;
}

button {
  margin-top: 10px;
  border-width: 0;
  padding: 5px 20px;
}

.btn-basic {
  background-color: #7de3fc;
}

.btn-white {
  background-color: white;
  color: #1dbfed; 
}

.painExp {
  margin-top: -10px;
}

.danger {
  color: red;
  background-color: white;
  padding-top: 20px;
  padding-bottom: 15px;
}

.navbar-header {
  margin-left: 10px;
}


